.wrapper {
  border: 1px solid #ccc;
  border-right: 0;
  border-bottom: 0;
  padding: 4px;
  white-space: nowrap;
  box-sizing: border-box;
  line-height: 1em;
  text-align: center;
  position: relative;

  input {
    z-index: 10;
    position: absolute;
    top: 0;
    left: 0;
    width: 100%;
    height: 100%;
    box-sizing: border-box;
    border: 1px solid #258cff;
    border-radius: 0;
    -webkit-appearance: none;
    outline: none;
    padding: 0 8px;
    text-align: center;
  }
}

.selected {
  background: aliceblue;
}
